import { io } from 'socket.io-client';

// Environment-based socket URL configuration
const getSocketUrl = () => {
  // Check if we're in production mode
  const isProduction = process.env.NODE_ENV === 'production' ||
                      window.location.hostname !== 'localhost';

  if (isProduction) {
    // Production: Use live server
    return 'https://TaskFlowPro.onrender.com';
  }

  // Development: Check if running in Capacitor (mobile app)
  if (window.Capacitor) {
    return 'http://192.168.1.2:3001';
  }

  // For web browser in development
  return 'http://localhost:3001';
};

const DEFAULT_URL = process.env.REACT_APP_BACKEND_URL || getSocketUrl();

export const socket = io(DEFAULT_URL, {
  autoConnect: true,
  transports: ['websocket'],
});

function authenticate() {
  try {
    const token = localStorage.getItem('token');
    if (token) {
      socket.emit('authenticate', token);
    }
  } catch (_) {}
}

socket.on('connect', () => {
  authenticate();
});

// Re-authenticate when storage token changes (basic approach)
window.addEventListener('storage', (e) => {
  if (e.key === 'token') authenticate();
});

export function reauthenticateSocket() {
  authenticate();
}
